@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body{margin:0;font-family:'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#fff;background-color:#0F335F}

.exchanger{padding:75px 30px;display:flex}.exchanger input{font-size:16px;line-height:42px;background:#FFFFFF;padding:0 0 0 16px;border-radius:4px;border:none;outline:none;box-shadow:0px 4px 4px rgba(51,51,51,0.04),0px 4px 16px rgba(51,51,51,0.08);display:block}.exchanger input::-webkit-outer-spin-button,.exchanger input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.exchanger ::-webkit-input-placeholder{color:#CBC9C9;font-family:'Poppins', sans-serif;font-size:14px;font-weight:300;line-height:24px;letter-spacing:0px;text-align:left}.exchanger :-ms-input-placeholder{color:#CBC9C9;font-family:'Poppins', sans-serif;font-size:14px;font-weight:300;line-height:24px;letter-spacing:0px;text-align:left}.exchanger ::placeholder{color:#CBC9C9;font-family:'Poppins', sans-serif;font-size:14px;font-weight:300;line-height:24px;letter-spacing:0px;text-align:left}.exchanger .converter{padding:10px 20px;display:flex;flex-direction:column}.exchanger .result{text-align:left;padding:0 10px;margin-bottom:42px}.exchanger .result .from-text{color:#aaa}.exchanger .result .to-text{font-size:2em;font-weight:600}.exchanger .currency-container{display:flex}.exchanger .currency-value{margin:10px}.exchanger .currency{color:#454545;position:relative;z-index:99;width:25vw;max-width:300px;margin:10px}.exchanger .currency.from-currency{z-index:100}.exchanger .exchange-history{padding:0 20px;min-width:50vw;flex:1 1}.exchanger .history-chart{color:#000}

.app{text-align:center;height:100vh}.app .app-header{font-size:1.8em;font-weight:700;background:rgba(0,0,0,0.2);padding:10px 20px;box-sizing:border-box;position:-webkit-sticky;position:sticky;top:0;z-index:200}.app .app-content{min-height:calc(100vh - 200px)}.app .footer{background:#0F335F;padding:20px;width:100%}.app .footer a{color:inherit;text-decoration:none}.app .footer a:hover{color:inherit}.app .footer .de-emphasize{color:#aaa;font-size:12px}.app .footer .social svg{margin:5px}

